<template>
  <div>
    <el-card>
      <el-form :model="queryParam" label-width="0px" size="small " @keyup.enter.native="getModelList">
        <el-row>
          <el-col :span="5">
            <el-form-item label="">
              <el-input v-model="queryParam.Warehouseno" placeholder="仓库编码" clearable></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="5">
            <el-form-item label="">
              <el-input v-model="queryParam.Warehousename" placeholder="仓库名称" clearable></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="5">
            <el-form-item  label="">
              <el-input v-model="queryParam.Materialno" placeholder="物料编码" clearable></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="5">
            <el-form-item  label="">
              <el-input v-model="queryParam.Batchno" placeholder="批次" clearable></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="5">
            <el-form-item  label="">
              <el-input v-model="queryParam.Barcode" placeholder="条码" clearable></el-input>
            </el-form-item>
          </el-col>

          <el-col :span="9">
            <el-form-item label="">
              <el-date-picker v-model="queryParam.Createtime" type="daterange" range-separator="至"
                start-placeholder="开始日期" end-placeholder="结束日期">
              </el-date-picker>
            </el-form-item>
          </el-col>

          <el-col :span="5">
            <el-form-item label-width="0">
              <el-button icon="el-icon-search" type="primary" @click="getModelList">查询</el-button>
              <!-- <el-button icon="el-icon-refresh-right" type="primary">重置</el-button> -->
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-card>
    <!-- table区域 -->
    <el-card body-style="padding:2px;">
      <el-table border :data="Data" :header-cell-style="{ padding: '2px', background: '#f6f6f6' }" v-loading="loading"
        :cell-style="{ padding: '2px' }" style="width: 100%;">
        <template v-for="item in columns">
          <el-table-column :key="item.prop" :prop="item.prop" :label="item.label" :width="item.width"
            v-if="item.colvisible" show-overflow-tooltip>
          </el-table-column>
        </template>
        <el-table-column width="100" fixed="right" label="操作">
          <template slot-scope="scope">
            <el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
            <el-popconfirm title="确定删除吗？"  @onConfirm="handleDelete(scope.row)">
              <el-button slot="reference" type="text" size="small" class="del-btn">删除</el-button>
              <!-- <el-button slot="reference">删除</el-button> -->
            </el-popconfirm>
            
          </template> 
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 分页区域 -->
    <pagination :total="PageData.totalCount" :fpage-size.sync="PageData.pageSize"
      :fcurrent-page.sync="PageData.currentPage" @pagination="getModelList" />

  </div>
</template>

<script>
  import {
    ALFModelListMixins
  } from "@/mixins/ALFModelListMixins";
  import Pagination from "@/components/Pagination";
  
  export default {
    mixins: [ALFModelListMixins],
    components: {
      Pagination      
    },
    data() {
      return {
        queryParam: {
          Warehouseno: "",
          Warehousename: "",
          Createtime: ""
        },
        apiUrl: {
          query: "/Warehouse/GetT_WarehouseListByPage",
          delete: "/Warehouse/DeleteT_WarehouseAsync"          
        },
        columns: [{
            label: "Id",
            prop: "Id",
            colvisible: false
          },
          {
            label: "仓库编码",
            prop: "Warehouseno",
            colvisible: true
          },
          {
            label: "仓库名称",
            prop: "Warehousename",
            colvisible: true
          },
          {
            label: "仓库描述",
            prop: "Locationdesc",
            colvisible: true
          },
          {
            label: "联系人",
            prop: "Contactuser",
            colvisible: true
          },
          {
            label: "联系电话",
            prop: "Contactphone",
            colvisible: true
          },
          {
            label: "地址",
            prop: "Address",
            colvisible: true
          },
          {
            label: "先进先出",
            prop: "StrFifo",
            colvisible: true
          },
          {
            label: "创建人",
            prop: "Creater",
            colvisible: true
          },
          {
            label: "创建时间",
            prop: "Createtime",
            colvisible: true
          }
        ]
      };
    }
  };

</script>

<style lang="scss" scoped>
  >>>.element.style {
    margin: 0;
  }

  .el-dropdown-link {
    cursor: pointer;
    color: #409eff;
  }

  .el-icon-arrow-down {
    font-size: 12px;
  }

  >>>.el-dialog {
    margin: 5vh auto 50px !important;
  }

  >>>.el-dialog__title {
    font-size: 14px;
    color: #fff;
  }

  >>>.el-dialog__header {
    background-color: #3b9bf5e6;
  }

  .el-form-item {
    margin: 10px;
  }

</style>



